import React, { useState } from "react";

export default function Login({ onLogin }) {
  const initialData = {
    email: '',
    password: '',
  }

  const [profileData, setProfileData] = useState(initialData);

  const handleChange = (e) => {
    const { name, value } = e.target
    setProfileData(profileData => ({
      ...profileData,
      [name]: value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault()

    if (!profileData.password || !profileData.email) {
      return;
    }

    onLogin(profileData)
  }

  return (
    <form className="login" onSubmit={handleSubmit}>
      <h1 className="login__title">–í—Ö–æ–¥</h1>
      <input autoComplete="on" className="login__email-input" placeholder="Email" id="email" name="email" type="email" value={profileData.email} onChange={handleChange} minLength="2" maxLength="40" required />
      <input autoComplete="on" className="login__password-input" placeholder="–ü–∞—Ä–æ–ª—å" id="password" name="password" type="password" value={profileData.password} onChange={handleChange} minLength="2" maxLength="40" required />
      <button className="login__button" type="submit">–í–æ–π—Ç–∏</button>
    </form>
  )
}
