import React, { useState, useEffect } from "react";
import logo from '../images/Logo-white.svg';
import { Link, Route } from 'react-router-dom';

function Header({ email, onSignOut, loggedIn }) {
  //* –°–æ—Å—Ç–æ—è–Ω–∏—è
  const [isClicked, setIsClicked] = useState(false);
  const [windowWidth, setWindowWidth] = useState(0);

  //* –ü—Ä–æ–≤–µ—Ä–∫–∞ —à–∏—Ä–∏–Ω—ã –æ–∫–Ω–∞ –ø—Ä–æ—Å–º–æ—Ç—Ä–∞, –µ—Å–ª–∏ –±–æ–ª—å—à–µ 767 —Ç–æ –±—É—Ä–≥–µ—Ä-–º–µ–Ω—é —Å—á–∏—Ç–∞–µ—Ç—Å—è –æ—Ç–∫—Ä—ã—Ç—ã–º. 
  //* –ë–µ–∑ –∫–æ–Ω—Ç—Ä–æ–ª—è —à–∏—Ä–∏–Ω—ã –æ–∫–Ω–∞ –ø—Ä–æ—Å–º–æ—Ç—Ä–∞, –µ—Å–ª–∏ –≤ –º–æ–±–∏–ª—å–Ω–æ–π –≤–µ—Ä—Å–∏–∏ –∑–∞–∫—Ä—ã—Ç—å –±—É—Ä–≥–µ—Ä –º–µ–Ω—é –∏ —Ä–∞–∑–≤–µ—Ä–Ω—É—Ç—å –±–æ–ª—å—à–æ–π —ç–∫—Ä–∞–Ω, —Ç–æ –ø–æ–ª–µ –∏–º–µ–π–ª–∞ –∏ –∫–Ω–æ–ø–∫–∞ –∏–º–µ—é—Ç display: none;
  let resizeWindow = () => {
    setWindowWidth(window.innerWidth);
    if(windowWidth >= 767) {
      setIsClicked(true);
    } 
  };

 //* –ü—Ä–æ–≤–µ—Ä–∫–∞ —à–∏—Ä–∏–Ω—ã –æ–∫–Ω–∞ –ø—Ä–∏ –º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤
  useEffect(() => {
    resizeWindow();
    window.addEventListener("resize", resizeWindow);
    return () => window.removeEventListener("resize", resizeWindow);
  }, [resizeWindow]);

  function handleClickMenu() {
    setIsClicked(!isClicked)
  }

  return (
    <header className="header">
      <img className="header__logo" alt="–õ–æ–≥–æ—Ç–∏–ø" src={logo} />
      <Route path="/sign-in">
        <Link to='sign-up' className={`${loggedIn ? 'header__link' : 'header__link header__link_login'}`}>–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</Link>
      </Route>
      <Route path="/sign-up">
        <Link to='sign-in' className={`${loggedIn ? 'header__link' : 'header__link header__link_login'}`}>–í–æ–π—Ç–∏</Link>
      </Route>
      <Route exact path="/">
        <div className='header__burger-menu'>
          <button type="button" className={`${isClicked ? 'header__button header__button_close' : 'header__button header__button_burger'}`} onClick={handleClickMenu}>
          <span></span>
          </button>
        </div>
        <div className={`${isClicked ? 'header__user-menu' : 'not-active'}`}>
          <p className='header__email'>{email}</p>
          <button
            onClick={() => {
              onSignOut();
              handleClickMenu();
            }}
            className='header__link header__link_button'>–í—ã–π—Ç–∏</button>
        </div>
      </Route>
    </header>
  )
}

export default Header;
