import React, { useState, useContext, useEffect } from 'react';
import PopupWithForm from "./PopupWithForm";
import CurrentUserContext from '../contexts/CurrentUserContext';

function EditProfilePopup({ isOpen, onClose, onUpdateUser, isDataLoad }) {
    const [profileName, setProfileName] = useState('');
    const [profileDescription, setDescription] = useState('');

    const currentUser = useContext(CurrentUserContext);

    const { name, about } = currentUser;

    function handleChangeName(e) {
        setProfileName(e.target.value);
    }

    function handleChangeDescription(e) {
        setDescription(e.target.value);
    }

    function handleSubmit(e) {
        e.preventDefault();

        //* –ü–µ—Ä–µ–¥–∞—ë–º –∑–Ω–∞—á–µ–Ω–∏—è —É–ø—Ä–∞–≤–ª—è–µ–º—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤–æ –≤–Ω–µ—à–Ω–∏–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫
        onUpdateUser({
            name: profileName,
            about: profileDescription,
        });
    }

    useEffect(() => {
        setProfileName(name);
        setDescription(about);
    }, [currentUser]);

    return (
        <PopupWithForm
            name="profile"
            id="editProfile"
            title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ø—Ä–æ—Ñ–∏–ª—å"
            isOpen={isOpen}
            onClose={onClose}
            onSubmit={handleSubmit}
            submitText={isDataLoad ? '–°–æ—Ö—Ä–∞–Ω—è–µ–º...' : '–°–æ—Ö—Ä–∞–Ω–∏—Ç—å –ø—Ä–æ—Ñ–∏–ª—å'}
        >
            <label className="popup__form-field">
                <input
                    value={profileName || ''} onChange={handleChangeName}
                    name="name"
                    placeholder="–ò–º—è"
                    required
                    maxLength="40"
                    className="popup__input"
                    id="popup__name-input"
                    type="text"
                />
                <span
                    id="popup__name-input-error"
                    className="popup__input-error"
                ></span>
            </label>
            <label className="popup__form-field">
                <input
                    value={profileDescription || ''} onChange={handleChangeDescription}
                    name="job"
                    placeholder="–ü—Ä–æ—Ñ–µ—Å—Å–∏—è"
                    required
                    maxLength="200"
                    className="popup__input"
                    id="popup__job-input"
                    type="text"
                />
                <span
                    id="popup__job-input-error"
                    className="popup__input-error"
                ></span>
            </label>
        </PopupWithForm>
    )
}

export default EditProfilePopup;