import React, { useContext } from "react";
import CurrentUserContext from '../contexts/CurrentUserContext';

function Card({ card, likeCounter, onCardClick, onCardDelete, onCardLike }) {

  const currentUser = useContext(CurrentUserContext);

  //* –û–ø—Ä–µ–¥–µ–ª—è–µ–º, —è–≤–ª—è–µ–º—Å—è –ª–∏ –º—ã –≤–ª–∞–¥–µ–ª—å—Ü–µ–º —Ç–µ–∫—É—â–µ–π –∫–∞—Ä—Ç–æ—á–∫–∏
  const isOwn = card.owner._id === currentUser._id;

  //* –°–æ–∑–¥–∞—ë–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é, –∫–æ—Ç–æ—Ä—É—é –ø–æ—Å–ª–µ –∑–∞–¥–∞–¥–∏–º –≤ `className` –¥–ª—è –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è
  const cardDeleteButtonClassName = (
    `elements__delete-button ${isOwn ? 'elements__delete-button_visible' : 'elements__delete-button_hidden'}`
  );

  //* –û–ø—Ä–µ–¥–µ–ª—è–µ–º, –µ—Å—Ç—å –ª–∏ —É –∫–∞—Ä—Ç–æ—á–∫–∏ –ª–∞–π–∫, –ø–æ—Å—Ç–∞–≤–ª–µ–Ω–Ω—ã–π —Ç–µ–∫—É—â–∏–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º
  const isLiked = card.likes.some(i => i._id === currentUser._id);

  //* –°–æ–∑–¥–∞—ë–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é, –∫–æ—Ç–æ—Ä—É—é –ø–æ—Å–ª–µ –∑–∞–¥–∞–¥–∏–º –≤ `className` –¥–ª—è –∫–Ω–æ–ø–∫–∏ –ª–∞–π–∫–∞
  const cardLikeButtonClassName = (
    `elements__like-button ${isLiked ? 'elements__like-button_active heartbeat' : ''}`
  );

  function handleClick() {
    onCardClick(card);
  }

  function handleLikeClick() {
    onCardLike(card)
  }

  function handleDeleteClick() {
    onCardDelete(card)
  }

  return (
    <li className="elements__list-item">
      <img
        src={card.link}
        className="elements__picture"
        title="–ü–æ—Å–º–æ—Ç—Ä–µ—Ç—å —Ñ–æ—Ç–æ–≥—Ä–∞—Ñ–∏—é"
        alt={card.name}
        onClick={handleClick}
        loading="lazy"
      />
      <div className="elements__container">
        <h2 className="elements__text">{card.name}</h2>
        <button
          type="button"
          title="–£–¥–∞–ª–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É"
          className={cardDeleteButtonClassName}
          onClick={handleDeleteClick}
        ></button>
        <div className="elements__like-container">
          <button
            type="button"
            className={cardLikeButtonClassName}
            onClick={handleLikeClick}
          ></button>
          <span className="elements__like-counter">{likeCounter}</span>
        </div>
      </div>
    </li>
  );
}

export default Card;