import React, { useState} from "react";
import { Link } from 'react-router-dom';

export default function Register({ onRegister }) {
  
  const initialData = {
    email: '',
    password: '',
  }

  const [profileData, setProfileData] = useState(initialData);

  const handleChange = (e) => {
    const { name, value } = e.target
    setProfileData(profileData => ({
      ...profileData,
      [name]: value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault()
    onRegister(profileData)
  }

  return (
    <form className="register" onSubmit={handleSubmit}>
      <h1 className="register__title">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</h1>
      <input autoComplete="on" className="register__email-input" placeholder="Email" id="email" name="email" type="email" value={profileData.email} onChange={handleChange} minLength="2" maxLength="40" required />
      <input autoComplete="on" className="register__password-input" placeholder="–ü–∞—Ä–æ–ª—å" id="password" name="password" type="password" value={profileData.password} onChange={handleChange} minLength="2" maxLength="40" required />
      <button type="submit" className="register__button">–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</button>
      <Link to="/sign-in" className="register__link">–£–∂–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã? –í–æ–π—Ç–∏</Link>
    </form>
  )
}
