import React, { useState } from 'react';
import PopupWithForm from "./PopupWithForm";

function AddPlacePopup({ isOpen, onClose, onAddCard, isDataLoad }) {

    const [placeName, setPlaceName] = useState('');
    const [placeLink, setPlaceLink] = useState('');


    function handleChangePlaceName(e) {
        setPlaceName(e.target.value);
    }

    function handleChangePlaceLink(e) {
        setPlaceLink(e.target.value);
    }

    function handleSubmit(e) {
        e.preventDefault();

        //* –ü–µ—Ä–µ–¥–∞—ë–º –∑–Ω–∞—á–µ–Ω–∏—è —É–ø—Ä–∞–≤–ª—è–µ–º—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤–æ –≤–Ω–µ—à–Ω–∏–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫
        onAddCard({
            name: placeName,
            link: placeLink,
        });
    }

    React.useEffect(() => {
        setPlaceName('');
        setPlaceLink('');
    }, [isOpen]);

    return (
        <PopupWithForm
            isOpen={isOpen}
            onClose={onClose}
            onSubmit={handleSubmit}
            name="newCard"
            id="addCard"
            title="–ù–æ–≤–æ–µ –º–µ—Å—Ç–æ"
            submitText={isDataLoad ? '–î–æ–±–∞–≤–ª—è–µ–º...' : '–î–æ–±–∞–≤–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É'}
        >
            <label className="popup__form-field">
                <input
                    value={placeName} onChange={handleChangePlaceName}
                    name="placeName"
                    className="popup__input"
                    id="popup__placeName-input"
                    required
                    minLength="1"
                    maxLength="30"
                    placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏"
                    type="text"
                />
                <span
                    id="popup__placeName-input-error"
                    className="popup__input-error"
                ></span>
            </label>
            <label className="popup__form-field">
                <input
                    value={placeLink} onChange={handleChangePlaceLink}
                    name="placeLink"
                    className="popup__input"
                    id="popup__placeLink-input"
                    required
                    placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É"
                    type="url"
                />
                <span
                    id="popup__placeLink-input-error"
                    className="popup__input-error"
                ></span>
            </label>
        </PopupWithForm>
    )
}

export default AddPlacePopup;