import React, { useContext } from "react";
import Card from "./Card";
import CurrentUserContext from '../contexts/CurrentUserContext';
import Spinner from './Spinner/Spinner';

function Main({ onEditAvatar, onEditProfile, onAddPlace, onCardClick, onCardDelete, onCardLike, cards, loading }) {

  const { name, about, avatar } = useContext(CurrentUserContext);

  return (
    <main className="content">
      { loading ? <Spinner /> :
        <>
          <section className="profile">
            <div className="profile__avatar-edit">
              <img
                className="profile__avatar"
                title="–ê–≤–∞—Ç–∞—Ä –ø—Ä–æ—Ñ–∏–ª—è"
                src={avatar}
                alt="–ê–≤–∞—Ç–∞—Ä"
              />
              <button
                title="–ó–∞–≥—Ä—É–∑–∏—Ç—å –Ω–æ–≤—ã–π –∞–≤–∞—Ç–∞—Ä"
                className="profile__avatar-edit-button"
                onClick={onEditAvatar}
              ></button>
            </div>
            <div className="profile__info">
              <h1 id="profile__name" className="profile__name">
                {name}
              </h1>
              <p id="profile__job" className="profile__job">
                {about}
              </p>
              <button
                type="button"
                title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ø—Ä–æ—Ñ–∏–ª—å"
                className="profile__edit-button"
                onClick={onEditProfile}
              ></button>
            </div>
            <button
              type="button"
              title="–î–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤—É—é —Ñ–æ—Ç–æ–≥—Ä–∞—Ñ–∏—é"
              className="profile__add-button"
              onClick={onAddPlace}
            ></button>
          </section>
          <section className="elements">
            <ul className="elements__list">
              {cards.map((card) => (
                <Card
                  key={card._id}
                  card={card}
                  onCardClick={onCardClick}
                  onCardDelete={onCardDelete}
                  likeCounter={card.likes.length}
                  onCardLike={onCardLike}
                />
              ))}
            </ul>
          </section>
        </>}
    </main>
  );
}

export default Main;
